$jobListWidth: 300px;

body {
  background-color: rgb(200, 200, 200);
  font-family: "Source Sans Pro", sans-serif;
}

#app {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

#viewport {
  display: flex;
  flex-direction: row;
  height: 100%;
  overflow: hidden;
}

#job-list {
  border-right: 1px solid rgba(0, 0, 0, 0.125);
  flex: 0 0 auto;
  overflow-x: hidden;
  overflow-y: scroll;
  transition: margin-left 0.5s ease-in-out;
  width: $jobListWidth;
  &.hidden {
    margin-left: -$jobListWidth;
  }
  .list-group {
    .list-group-item {
      border-right: none;
    }
  }
}

#workbench-contents {
  flex: 1 1 auto;
  min-width: 0;
}

.file-list .first-column {
  width: 1rem;
  font-weight: bold;
}

.file-list .third-column,
.file-list .fourth-column,
.file-list .fifth-column {
  width: 8rem;
}

.file-list .second-column {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.file-list thead tr th,
.file-list tbody tr td {
  vertical-align: middle;
}

.file-list {
  margin-bottom: 0;
  max-height: 450px;
  overflow-y: auto;
}

.file-list-container {
  height: 300px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.file-list table {
  margin-bottom: 0;
}

.dropzone-wrapper {
  height: 10rem;
  border-style: dashed;
  border-width: 1px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.dropbox-container span {
  display: block;
}

.dropzone-wrapper .icon-arrow-up-circle {
  font-size: 3rem;
}

.file-list .third-column .badge {
  text-transform: uppercase;
}

.file-list .sixth-column {
  text-align: center;
  width: 1rem;
  font-size: 1.5rem;
}

.file-list .sixth-column a:hover {
  text-decoration: none;
}

.btn {
  &.btn-primary,
  &.btn-outline-secondary {
    width: 100px;
    .spinner-border {
      height: 1rem;
      width: 1rem;
    }
  }
}
