.zoning {
  height: 100%;
  display: flex;
  flex-direction: column;
  .contents {
    flex: 1 1 auto;
    display: flex;
    flex-direction: row;
    height: 100%;
    .file-picker {
      width: 20%;
    }
    .renderer {
      background-color: lightgrey;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
      min-height: 1px;
      min-width: 1px;
      position: relative;
      canvas {
        background: transparent;
        bottom: 0;
        height: 100%;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
        z-index: 100;
      }
      .loading-wrapper {
        background-color: rgba(0, 0, 0, 0.5);
        bottom: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2000;
        .spinner-border {
          height: 100px;
          width: 100px;
        }
      }
      .portal {
        background-color: transparent;
        z-index: 1000;
      }
      .toolbar {
        align-items: center;
        background-color: #666;
        color: white;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 0 10px;
        min-height: 47px;
        padding: 5px 15px;
        .btn {
          width: 100px;
        }
      }
    }
    .page-preview {
      background-color: #666;
      color: #fff;
      overflow-x: hidden;
      overflow-y: scroll;
      padding: 1.5rem;
      width: 250px;
      .image-preview {
        border: 1px solid transparent;
        margin-bottom: 0.5rem;
        padding: 0.5rem 0.75rem;
        position: relative;
        text-align: center;
        &.active {
          border-color: #fff;
          &:hover {
            border-color: #fff;
          }
        }
        &:hover {
          border-color: #ccc;
        }
        > div {
          height: 210px;
          margin-bottom: 0.5rem;
          width: 100%;
        }
      }
    }
  }
  .footer {
    padding: 0.5rem 0;
    text-align: center;
  }
}
